<!--
    Component toolbar browser
-->
<template>
  <div class="ToolBarRedComponent">
    <button
      class="ToolBarButtonsDefault"
      v-on:click="baseController.loadDataTableWithHandlerViewError()"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.refresh')" />
      <i class="fas fa-sync-alt"></i>
    </button>

    <button
      class="ToolBarButtonsDefault"
      v-on:click="baseController.resetOrdersWithHandlerViewError()"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.resetOrders')" />
      <i class="fas fa-sort"></i>
    </button>

    <button
      class="ToolBarButtonsDefault"
      v-on:click="baseController.resetDatatablesWithHandlerViewError()"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.resetTable')" />
      <i class="fas fa-recycle"></i>
    </button>

    <button
      v-if="renderApplyFilters"
      class="ToolBarButtonsDefault"
      v-on:click="baseController.applyFiltersWithHandlerViewError()"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.applyFilters')" />
      <i class="fas fa-search-plus"></i>
    </button>

    <button
      v-if="renderResetFilters"
      class="ToolBarButtonsDefault"
      v-on:click="baseController.resetFiltersWithHandlerViewError()"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.resetFilters')" />
      <i class="fas fa-search-minus"></i>
    </button>

    <button
      v-if="renderAdd"
      class="ToolBarButtonsDefault"
      v-on:click="baseController.goAddWithHandlerViewError()"
      type="button"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.goAdd')" />
      <i class="fas fa-plus"></i>
    </button>

    <!--show views table-->
    <button
      v-if="renderTableViews"
      class="ToolBarButtonsDefault"
      v-on:click="baseController.changeTableView()"
    >
      <ToolTipComponent :value="i18n('rfvue.toolbar.chagenTableView')" />
      <i :class="(baseController.tableView == 'Table' ? 'fas fa-table' : 'fas fa-th')"></i>
    </button>

    <slot name="afterContentTemplate"></slot>
  </div>
</template>
<script>
import BaseComponent from "../base/BaseComponent";
import ToolTipComponent from "../tooltip/ToolTipComponent";

export default {
  extends: BaseComponent,
  name: "ToolBarBrowserComponent",
  props: {
    baseController: {
      type: Object,
      default: null
    },
    renderAdd: {
      type: Boolean,
      default: true
    },
    renderTableViews: {
      type: Boolean,
      default: false
    },
    renderApplyFilters: {
      type: Boolean,
      default: true
    },
    renderResetFilters: {
      type: Boolean,
      default: true
    }
  },
  methods: {},
  components: {
    ToolTipComponent
  }
};
</script>
<style scoped>
.ToolBarLisToolBarBrowserComponent {
  width: 100%;
  float: left;
  clear: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
</style>