import CommonsUtils from "../utils/CommonsUtils";

/**
 * Class to store data for columns
 */
export default class Column {
  key;
  title;
  width;
  sortable;
  style;
  keySplit;
  keyLength;
  sortDirecction;

  /**
   * Constructor for class Column
   * @param key is key for access column
   * @param title title to show in column
   * @param width width for column in px
   * @param sortable true or false is sortable
   */
  constructor(key, title, width, sortable) {
    this.key = key;
    this.title = title || "";
    this.width = width || 300;
    this.sortable = sortable || false;
    this.keySplit = [];
    this.keyLength = 0;
    if (CommonsUtils.isNotNull(this.key)) {
      this.keySplit = this.key.split("_");
      this.keyLength = this.keySplit.length;
    }
    this.sortDirecction = null;
  }
}
