<!--

Component body pages

-->

<template>
  <div :class="'BodyPageComponent '+ styleClass" :style="getStyle()">
    <slot name="bodyPageContentTemplate"></slot>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";

export default {
  extends: BaseComponent,
  name: "BodyPageComponent",
  props: {
    styleClass: {
      type: String,
      // Default styleClass
      default: ""
    },
    toolbarHeight: {
      type: Number,
      // Default heigth
      default: 30
    }
  },
  methods: {
    /**
     * Method for get style for body page
     * @returns style for body page
     */
    getStyle: function() {
      return "height: " + "calc( 100% - " + this.toolbarHeight + "px);";
    }
  }
};
</script>

<style scoped>
.BodyPageComponent {
  width: 100%;
  overflow: auto;
  margin: 0;
  padding: 0;
  float: left;
  box-sizing: border-box;
}
</style>