/**
 * Class filter data for queries in database
 */
export default class Filter {
  property;
  type;
  value;
  andOr;
  alias;
  propertyData;

  constructor(property, type, value, andOr, alias, propertyData) {
    this.property = property;
    this.type = type;
    this.value = value;
    this.andOr = andOr;
    this.alias = alias;
    this.propertyData = propertyData;
    this.splitToken = ",";
    this.hashtagFilter = false;
  }
}
