import BaseController from "./BaseController";
import ConstantsStateAccess from "../constants/ConstantsStateAccess";
import CommonsUtils from "../utils/CommonsUtils";
import Limit from "../beans/Limit";
/**
 * Base browser controller
 */
export default class BaseBrowserController extends BaseController {
  constructor(baseViewController) {
    super(baseViewController);
    // Data list for controller
    this.data = [];
    // Orders apply find data
    this.orders = [];
    // Limits find data
    this.limits = null;
    // Records page
    this.recordsPage = this.baseViewController.getVueContext().totalRecordsFindListDefault;
    // First element page
    this.first = -1;
    // Element for controller
    this.element = null;
    // Total records find
    this.totalRecords = 0;
    // Number of pages
    this.numberOfPages = 0;
    // Page view data
    this.page = 0;
    // Array pages view data
    this.pages = [];
    // Number of pages vivisble
    this.pagesVisibles = this.baseViewController.getVueContext().pageVisiblesInListDefault;
    // Extra param controller
    this.extraParam = {};
    // Old selelect element
    this.oldElement = null;
    // Table component for browsers
    this.tableComponent = null;
    // Columns table
    this.columns = [];
    // type table view
    this.tableView = "Table";

    this.setDefaultOrders();
    this.setDefaulFetchs();
    this.setDefaultFilters();
    this.setFiltersBrowser();

    // Load columns for browser
    this.loadColumns();
  }

  /**
   * Load columns for browser
   */
  async loadColumns() {}

  /**
   * Method for load data
   */
  async loadData() {
    this.oldElement = this.element;
    switch (this.stateAccess) {
      case ConstantsStateAccess.LIST:
        await this.loadList();
        break;
      case ConstantsStateAccess.READ:
        await this.loadRead();
        break;
      case ConstantsStateAccess.ADD:
        await this.loadNew();
        break;
      case ConstantsStateAccess.EDIT:
        await this.loadEdit();
        break;
      case ConstantsStateAccess.DELETE:
        await this.loadDelete();
        break;
    }
  }

  /**
   * Method to load new entity
   */
  async loadNew() {}

  /**
   * Method to load edit
   */
  async loadEdit() {}

  /**
   * Method to load delete
   */
  async loadDelete() {
    // In delete not change state access
    this.stateAccess = this.oldStateAccess;
  }

  /**
   * Method to go list with handler view error
   */
  async goListHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.goList();
      });
  }

  /**
   * Method to go list
   */
  async goList() {
    this.markForfixFocusFirstInput = true;
    await this.changeState(ConstantsStateAccess.LIST);
  }

  /**
   * Method to go read with handler view error
   */
  async goReadHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.goRead();
      });
  }

  /**
   * Method to go read
   */
  async goRead() {
    this.markForfixFocusFirstInput = true;
    await this.changeState(ConstantsStateAccess.READ);
  }

  /**
   * Method to go edit with handler view error
   */
  async goEditHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.goEdit();
      });
  }

  /**
   * Method to go edit
   */
  async goEdit() {
    this.markForfixFocusFirstInput = true;
    await this.changeState(ConstantsStateAccess.EDIT);
  }

  /**
   * Method to go add with handler view error
   */
  async goAddHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.goAdd();
      });
  }

  /**
   * Method to go add with handler view error
   */
  async goAddWithHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.goAdd();
      });
  }

  /**
   * Method to go add
   */
  async goAdd() {
    this.markForfixFocusFirstInput = true;
    await this.changeState(ConstantsStateAccess.ADD);
  }

  /**
   * Method to go delete with handler view error
   */
  async goDeleteWithHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.goDelete();
      });
  }

  /**
   * Method to go delete
   */
  async goDelete() {
    this.markForfixFocusFirstInput = true;
    await this.changeState(ConstantsStateAccess.DELETE);
  }

  /**
   * Method to load data for datatable with handler view error
   */
  async loadDataTableWithHandlerViewError() {
    const self = this;

    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.loadDataTable();
      });
  }

  /**
   * Method to load data for datatable
   */
  async loadDataTable() {
    await this.loadList();
    this.setDataInDatatable();
  }

  /**
   * Method to set data in datatable
   */
  setDataInDatatable() {
    if (CommonsUtils.isNotNull(this.tableComponent)) {
      this.tableComponent.totalRecords = this.totalRecords;
      this.tableComponent.numberOfPages = this.numberOfPages;
      this.tableComponent.page = this.page;
      this.tableComponent.pages = this.pages;
      this.tableComponent.first = this.first;
      this.tableComponent.dataInView = this.data;
    }
  }
  /**
   * Method to reset data load data list
   */
  resetLoadList() {
    this.totalRecords = 0;
    this.data = [];
    this.page = 1;
    this.first = 0;
    this.limits = new Limit(0, 0);
    this.pages = [];
    this.numberOfPages = 0;
    this.filters = [];
    this.calculatePages();
    this.setDataInDatatable();
  }

  /**
   * Method to calculate pages
   */
  calculatePages() {
    const numberOfPages = this.numberOfPages;
    const visiblePages = Math.min(this.pagesVisibles, numberOfPages);

    // calculate range, keep current in middle if necessary
    let start = Math.max(0, Math.ceil(this.page - visiblePages / 2)) + 1;
    const end = Math.min(numberOfPages - 1, start + visiblePages - 1) + 1;

    // check when approaching to last page
    // let delta = this.pageVisibles - (end - start + 1);
    const delta = this.pagesVisibles - (end - start);
    start = Math.max(0, start - delta);

    if (start === 0 && end > 0) {
      start = 1;
    }

    this.pages = [];

    for (let i = start; i <= end; i++) {
      this.pages.push(i);
    }
  }

  /**
   * Method to resolve filters
   * @returns filters appliyed
   */
  resolveFilters() {
    let filtersQuery = [];

    // Set default filters
    this.setDefaultFilters();

    if (
      CommonsUtils.isNotNull(this.defaultFilters) &&
      this.defaultFilters.length > 0
    ) {
      filtersQuery = this.defaultFilters;
    }

    if (CommonsUtils.isNotNull(this.filters) && this.filters.length > 0) {
      for (let i = 0; i < this.filters.length; i++) {
        filtersQuery.push(this.filters[i]);
      }
    }

    return filtersQuery;
  }

  /**
   * Method to resolve orders
   * @returns orders appliyed
   */
  resolveOrders() {
    let ordersQuery = [];

    if (
      CommonsUtils.isNotNull(this.defaultOrders) &&
      this.defaultOrders.length > 0
    ) {
      ordersQuery = this.defaultOrders;
    }

    if (CommonsUtils.isNotNull(this.orders) && this.orders.length > 0) {
      ordersQuery = this.orders;
    }

    return ordersQuery;
  }

  /**
   * Method to load list
   */
  async loadList() {
    if (CommonsUtils.isNull(this.service)) {
      await this.loadService();
    }
    const self = this;
    let dataLoad = [];
    let totalRecordsLoad = 0;
    let numberOfPagesLoad = 0;
    let pageLoad = 0;
    const limitsLoad = new Limit(0, 0);

    try {
      let responseService = await this.resolveCountAwait(async function() {
        return self.service.count(
          self.resolveFilters(),
          self.defaultFetchs,
          null,
          null
        );
      });

      totalRecordsLoad = responseService.data;
      numberOfPagesLoad = Math.ceil(totalRecordsLoad / this.recordsPage);

      if (totalRecordsLoad > 0) {
        if (this.first > -1) {
          pageLoad = Math.ceil(this.first / this.recordsPage);
        }

        if (pageLoad > numberOfPagesLoad) {
          pageLoad = 1;
        }

        if (pageLoad < 1) {
          pageLoad = 1;
        }

        limitsLoad.start = (pageLoad - 1) * this.recordsPage;

        if (limitsLoad.start < 0) {
          limitsLoad.start = 0;
        }

        limitsLoad.end = this.recordsPage;

        responseService = await this.resolveDataAwait(async function() {
          return self.service.list(
            self.resolveFilters(),
            self.defaultFetchs,
            self.resolveOrders(),
            limitsLoad
          );
        });

        dataLoad = responseService.data;

        // if (CommonsUtils.isNotNull(dataLoad)) {
        //   for (let i = 0; i < dataLoad.length; i++) {
        //     this.data.push(dataLoad[i]);
        //   }
        // }
      }

      this.totalRecords = totalRecordsLoad;
      this.numberOfPages = numberOfPagesLoad;
      this.page = pageLoad;
      this.limits = limitsLoad;
      this.data = dataLoad;
      this.calculatePages();
    } catch (ex) {
      console.error(ex);
      this.resetLoadList();
    }
  }

  /**
   * Method for go position in datatable with error view handler
   * @param position is position for register
   */
  async goPositionWithHandlerViewError(position) {
    const self = this;

    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.goPosition(position);
      });
  }

  /**
   * Method for go position in datatable
   * @param position is position for register
   */
  async goPosition(position) {
    if (CommonsUtils.isNull(this.service)) {
      await this.loadService();
    }
    if (position >= this.totalRecords) {
      position = this.totalRecords - 1;
    }
    if (position < 0) {
      position = 0;
    }
    const limit = new Limit(position, 1);

    const self = this;

    const responseService = await this.resolveDataAwait(async function() {
      return self.service.list(
        self.resolveFilters(),
        self.defaultFetchs,
        self.resolveOrders(),
        limit
      );
    });

    const data = responseService.data;

    if (CommonsUtils.arrayNotNull(data)) {
      this.element = data[0];
      this.first = position;
      this.goRead();
    } else {
      this.goList();
    }
  }
  /**
   * Method for go page
   * @param page is page to go
   */
  async goPage(page) {
    this.page = page;
    this.first = (page - 1) * this.recordsPage + this.recordsPage;
    this.loadDataTable();
  }
  /**
   * Method for reset orders
   */
  async resetOrders() {
    this.orders = [];
    await this.loadDataTable();
    this.tableComponent.resetOrders();
  }

  /**
   * Method for reset orders with handler view error
   */
  async resetOrdersWithHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.resetOrders();
      });
  }

  /**
   * Method for reset datatable
   */
  async resetDatatable() {
    this.orders = [];
    this.setFiltersBrowser();
    this.resetLoadList();
    await this.loadDataTable();
    this.tableComponent.resetOrders();
  }

  /**
   * Method for reset datatable with handler view error
   */
  async resetDatatablesWithHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.resetDatatable();
      });
  }

  /**
   * Method for change table view
   */
  changeTableView() {
    if (this.tableView === "Table") {
      this.tableView = "Grid";
    } else {
      this.tableView = "Table";
    }
  }
  /**
   * Method for set style row data
   * @param style default in row
   * @param rowData from row
   */
  styleRow(style, rowData) {
    rowData != null;
    return style;
  }

  /**
   * Method for apply filters
   */
  async applyFilters() {
    super.applyFilters();
    this.loadDataTable();
  }

  /**
   * Method for apply filters with handler view error
   */
  async applyFiltersWithHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.applyFilters();
      });
  }

  /**
   * Method for reset filters
   */
  async resetFilters() {
    super.resetFilters();
    this.loadDataTable();
  }

  /**
   * Method for reset filters with handler view error
   */
  async resetFiltersWithHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.resetFilters();
      });
  }

  /**
   * Method for clear data list
   */
  clearDataList() {
    this.data = [];
  }
}
