import ConstantsStateAccess from "../constants/ConstantsStateAccess";
import CommonsUtils from "../utils/CommonsUtils";
import ConstantsModals from "../constants/ConstantsModals";
import Limit from "./Limit";
import Filter from "./Filter";
import VueContext from "../context/VueContext";
/**
 * Base controller
 */
export default class BaseController {
  constructor(baseViewController) {
    // Base vue controller
    this.baseViewController = baseViewController;

    // State access
    this.stateAccess = ConstantsStateAccess.LIST;
    // Old State access
    this.oldStateAccess = ConstantsStateAccess.LIST;
    // Service
    this.service = null;
    // Is modal or not
    this.modal = false;
    // Key resolve data
    this.keyResolveData = "data";
    // Flat to check focus first input
    this.markForfixFocusFirstInput = true;
    //Default fetchs for find entities
    this.defaultFetchs = [];
    // Default filters for find entities
    this.defaultFilters = [];
    // Default order for find entities
    this.defaultOrders = [];
    // Filters find data
    this.filters = [];
    // Filters apply browser find data
    this.filtersBrowser = [];
  }

  /**
   * MEthod for translate text
   * @param {*} key for text in i18n
   * @param {*} params for add to text in i18n
   */
  i18n(key, params) {
    return this.baseViewController.i18n(key, params);
  }

  /**
   * Method for get service name
   * Must be string
   * @returns a name for service for load
   */
  getServiceName() {
    return "";
  }

  /**
   * Method for load service
   */
  async loadService() {
    if (!this.haveService()) {
      let service = VueContext.mapLazyImportServices[this.getServiceName()];
      if (CommonsUtils.isNotNull(service)) {
        this.service = await service.fnLazyImport();
      }
    }
  }

  /**
   * Method to know have service
   * @returns true if have service false if not
   */
  haveService() {
    return CommonsUtils.isNotNull(this.service);
  }

  /**
   * Method for validate data submint
   * @returns true if valid data, false if not
   */
  validateDataSubmit() {
    let valid = true;
    return valid;
  }
  /**
   * Method to set default orders
   */
  setDefaultOrders() {}

  /**
   * Method to set default fetchs
   */
  setDefaulFetchs() {}

  /**
   * Method to set default filters
   */
  setDefaultFilters() {}

  /**
   * Method to set filters on init browser
   */
  setFiltersBrowser() {}

  /**
   * Method to load data
   */
  async loadData() {}

  /**
   * Method for add message success
   */
  addMessageSucces(message) {
    this.baseViewController.addMessageSucces(message);
  }

  /**
   * Method for add message info
   */
  addMessageInfo(message) {
    this.baseViewController.addMessageInfo(message);
  }
  /**
   * Method for add message error
   */
  addMessageError(message) {
    this.baseViewController.addMessageError(message);
  }

  /**
   * Method for add message warning
   */
  addMessageWarning(message) {
    this.baseViewController.addMessageWarning(message);
  }

  /**
   * Method for change state
   * @param stateAccess state to chage
   */
  async changeState(stateAccess) {
    this.baseViewController.hideToolTips();
    this.oldStateAccess = this.stateAccess;
    this.stateAccess = stateAccess;
    // Load data when change state
    await this.loadData();
    this.baseViewController.setFocusFirstInput();
  }

  /**
   * Method to know is state
   * @param state to know is state
   * @param stateCheck is state to check is it
   * @returns true if state is true or false if not
   */
  isState(state, stateCheck) {
    return (
      CommonsUtils.isNotNull(state) &&
      CommonsUtils.isNotNull(stateCheck) &&
      state === stateCheck
    );
  }
  /**
   * Mhetod to know state is read
   * @returns true if state is read
   */
  isStateRead() {
    return this.isState(this.stateAccess, ConstantsStateAccess.READ);
  }
  /**
   * Mhetod to know state is edit
   * @returns true if state is edit
   */
  isStateEdit() {
    return this.isState(this.stateAccess, ConstantsStateAccess.EDIT);
  }
  /**
   * Mhetod to know state is add
   * @returns true if state is add
   */
  isStateAdd() {
    return this.isState(this.stateAccess, ConstantsStateAccess.ADD);
  }
  /**
   * Mhetod to know state is list
   * @returns true if state is list
   */
  isStateList() {
    return this.isState(this.stateAccess, ConstantsStateAccess.LIST);
  }
  /**
   * Mhetod to know state is report
   * @returns true if state is report
   */
  isStateReport() {
    return this.isState(this.stateAccess, ConstantsStateAccess.REPORT);
  }
  /**
   * Mhetod to know state is other
   * @returns true if state is other
   */
  isStateOther() {
    return this.isState(this.stateAccess, ConstantsStateAccess.OTHER);
  }
  /**
   * Method for apply filters
   */
  async applyFilters() {
    if (CommonsUtils.arrayNotNull(this.filtersBrowser)) {
      this.filters = [];
      for (let i = 0; i < this.filtersBrowser.length; i++) {
        this.filters.push(Object.assign({}, this.filtersBrowser[i]));
      }
    }
  }
  /**
   * Method for reset filters
   */
  async resetFilters() {
    this.filters = [];
    this.setFiltersBrowser();
  }

  /**
   * Method for resolve data acess await
   * @param {*} fnData
   * @param {*} keyResolveData
   */
  async resolveDataAccessAwait(fnData, keyResolveData) {
    return this.baseViewController.resolveDataAccessAwait(
      fnData,
      keyResolveData
    );
  }

  /**
   * Method to resolve data from queries http
   * @param fnData is the function to load data. Is async funcion
   */
  async resolveDataAwait(fnData) {
    return await this.resolveDataAccessAwait(fnData, this.keyResolveData);
  }

  /**
   * Method to resolve data count from queries http
   * @param  fnData is the function to load data. Is async funcion
   */
  async resolveCountAwait(fnData) {
    const dataReturn = await this.resolveDataAccessAwait(
      fnData,
      this.keyResolveData
    );
    if (dataReturn.statusCode !== 200) {
      dataReturn.data = 0;
    }
    return dataReturn;
  }

  /**
   * Method to select datatable modal
   * @param data
   */
  async selectDatatableModal(data) {
    let entity = null;
    const filter = new Filter("id", "=", data.id, "and", null);
    const listFilters = [];
    listFilters.push(filter);
    const self = this;
    let responseService = await this.resolveDataAwait(async function() {
      return self.service.list(
        listFilters,
        self.defaultFetchs,
        null,
        new Limit(0, 1)
      );
    });
    // If correct get entity
    if (responseService.statusCode === 200) {
      entity = responseService.data[0];
    } else {
      // if not correct load new entity
      responseService = await this.resolveDataAwait(async function() {
        return self.service.loadNew();
      });
      if (responseService.statusCode === 200) {
        entity = responseService.data;
      }
    }
    if (
      CommonsUtils.isNotNull(
        this.baseViewController.params[
          ConstantsModals.KEY_MODAL_COMPONENT_PARAM_BEAN_SELECT_TABLE
        ]
      )
    ) {
      this.baseViewController.params[
        ConstantsModals.KEY_MODAL_COMPONENT_PARAM_BEAN_SELECT_TABLE
      ][
        this.baseViewController.params[
          ConstantsModals.KEY_MODAL_COMPONENT_PARAM_FUNCTION_SELECT_TABLE
        ]
      ](entity);
    }
    this.baseViewController.closeModal();
  }
  /**
   * Method to know is modal
   */
  isModal() {
    return this.baseViewController.isModal();
  }
  /**
   * Method for validate container view
   * @returns true if valida, false if not
   */
  async validateContainerView() {
    let valid = await this.baseViewController
      .getVueContext()
      .validateContainer(this.baseViewController.getIdRed());
    return valid;
  }
  /**
   * Method for get id from view
   * @returns id from view
   */
  getId() {
    return this.baseViewController.getId();
  }

  /**
   * Method to knwo is electron app
   * @returns true if is electron app false if not
   */
  isElectronApp() {
    return this.baseViewController.getVueContext().isElectronApp();
  }
}
