import CommonsUtils from "./CommonsUtils";
import ConstantsStatusCodesHttp from "../constants/ConstantsStatusCodesHttp";
/**
 * Class for http utilities
 */
export default class HttpUtils {
  /**
   * Method to knwo status conde is in http status code oks, similar like 200, 201 ...
   * @param statusCode is the status code to check is ok
   * @return true is ok false is not ok or error produces
   */
  static isStatusCodeFamilyHttpOk(statusCode) {
    let valid = false;
    if (CommonsUtils.isNotNull(statusCode)) {
      try {
        valid =
          statusCode === ConstantsStatusCodesHttp.OK ||
          statusCode === ConstantsStatusCodesHttp.CREATED;
      } catch (ex) {
        console.error("Error to check status code http");
        console.error(ex);
      }
    }
    return valid;
  }
}
