<!--

Component toolbar pages

-->

<template>
  <div :class="'ToolBarPageComponent '+ styleClass" :style="getStyle()">
    <slot name="toolBarPageContentTemplate"></slot>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";

export default {
  extends: BaseComponent,
  name: "ToolBarPageComponent",
  props: {
    styleClass: {
      type: String,
      // Default styleClass
      default: ""
    },
    height: {
      type: Number,
      // Default heigth
      default: 30
    }
  },
  methods: {
    /**
     * Method for get style for body page
     * @returns style for body page
     */
    getStyle: function() {
      return "height: " + "" + this.height + "px;";
    }
  }
};
</script>

<style scoped>
.ToolBarPageComponent {
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  float: left;
  box-sizing: border-box;
  padding-left: 5px;
}
</style>