<template>
  <!--Table component-->

  <div class="TableComponent">
    <!-- Wrapper -->
    <div class="TableWrapperComponent">
      <!-- Grid -->
      <div class="TableGirdComponent" v-if="hasData() && typeView == 'Grid'">
        <div
          class="TableRowGridLine"
          v-for="(rowData, rowIndexTable) in dataInView"
          :key="rowIndexTable"
          :style="styleRow(rowData, false, '')"
        >
          <div class="TableRowGridLineActions" v-if="hasActions">
            <div class="TableRowGridLineActionsTitle">{{i18n('rfvue.table.actions')}}:</div>
            <button
              v-if="showSelectButton()"
              :class="'TableButtonComponent ' +(rowIndexTable == rowIndex ? ' TableButtonComponentSelected' : '')"
              type="button"
              v-on:click="onClickAcctionWithHandlerViewError(rowData, rowIndexTable, 'select')"
            >
              <ToolTipComponent :value="i18n('rfvue.table.selected')" />
              <i class="far fa-check-square"></i>
            </button>

            <button
              v-if="renderRead"
              :class="'TableButtonComponent ' +(rowIndexTable == rowIndex ? ' TableButtonComponentSelected' : '')"
              type="button"
              v-on:click="onClickAcctionWithHandlerViewError(rowData, rowIndexTable, 'read')"
            >
              <ToolTipComponent :value="i18n('rfvue.table.read')" />
              <i class="far fa-eye"></i>
            </button>

            <button
              v-if="renderEdit"
              :class="'TableButtonComponent ' +(rowIndexTable == rowIndex ? ' TableButtonComponentSelected' : '')"
              type="button"
              v-on:click="onClickAcctionWithHandlerViewError(rowData, rowIndexTable, 'edit')"
            >
              <ToolTipComponent :value="i18n('rfvue.table.edit')" />
              <i class="far fa-edit"></i>
            </button>

            <button
              v-if="renderDelete"
              :class="'TableButtonComponent ' +(rowIndexTable == rowIndex ? ' TableButtonComponentSelected' : '')"
              type="button"
              v-on:click="onClickAcctionWithHandlerViewError(rowData, rowIndexTable, 'delete')"
            >
              <ToolTipComponent :value="i18n('rfvue.table.delete')" />
              <i class="far fa-trash-alt"></i>
            </button>

            <slot name="afterRowAcctionsGrid"></slot>
          </div>

          <slot name="actionsRowGrid"></slot>

          <div class="TableRowGridLineContent">
            <slot name="tableGridLineTemplate" v-bind:rowData="rowData" v-bind:rowIndex="rowIndex"></slot>
          </div>
        </div>
      </div>

      <!-- Table -->
      <div class="TableTableComponent" v-if="hasData() && typeView == 'Table'">
        <!--Header -->
        <div class="TableComponentHeaders">
          <div class="TableHeadersComponent">
            <div class="TableComponentHeader" v-if="hasActions" :style="getWidthActions()">
              <div class="TableComponentHeaderTitle">{{i18n('rfvue.table.actions')}}</div>
            </div>

            <div
              v-for="(column, indexColumn) in columns"
              :key="indexColumn"
              :class="'TableComponentHeader ' +(column.sortable ? ' TableComponentHeaderSortable ' : '')"
              :style="'width:' + column.width + 'px;'"
              v-on:click="onClickColumnHeader(column)"
            >
              <div class="TableComponentHeaderTitle">
                {{column.title}}
                &nbsp;
                <!-- <li class="{{classColumnSortableIcon(column)}}"></li> -->
                <li v-if="column.sortable && column.sortDirecction == null" class="fas fa-sort"></li>
                <li
                  v-else-if="column.sortable && column.sortDirecction != null && column.sortDirecction == 'ASC'"
                  class="fas fa-sort-up"
                ></li>
                <li
                  v-else-if="column.sortable && column.sortDirecction != null && column.sortDirecction == 'DESC'"
                  class="fas fa-sort-down"
                ></li>
              </div>
            </div>
          </div>
        </div>
        <!-- rows -->
        <div
          class="TableComponentRow"
          :style="styleHeightRowInView"
          v-for="(rowData, rowIndexTable) in dataInView"
          :key="rowIndexTable"
        >
          <div
            :class="'TableRowsComponent '+ (rowIndexTable == rowIndex ? ' TableRowSelected ' : '')"
            v-on:click="clickRow(rowData, rowIndexTable);"
            v-on:dblclick="dblClickRow(rowData, rowIndexTable)"
          >
            <div
              v-if="hasActions"
              :class="(rowIndex % 2 == 0 ? 'TableRowComponent TableRowsComponentPar' : 'TableRowComponent TableRowsComponentInPar')"
              :style="styleRow(rowData, true)"
            >
              <button
                v-if="showSelectButton()"
                :class="'TableButtonComponent ' +(rowIndexTable == rowIndex ? 'TableButtonComponentSelected' : '')"
                type="button"
                v-on:click="onClickAcctionWithHandlerViewError(rowData, rowIndexTable, 'select')"
              >
                <ToolTipComponent :value="i18n('rfvue.table.selected')" />
                <i class="far fa-check-square"></i>
              </button>

              <button
                v-if="renderRead"
                :class="'TableButtonComponent ' +(rowIndexTable == rowIndex ? ' TableButtonComponentSelected' : '')"
                type="button"
                v-on:click="onClickAcctionWithHandlerViewError(rowData, rowIndexTable, 'read')"
              >
                <ToolTipComponent :value="i18n('rfvue.table.read')" />
                <i class="far fa-eye"></i>
              </button>

              <button
                v-if="renderEdit"
                :class="'TableButtonComponent ' +(rowIndexTable == rowIndex ? ' TableButtonComponentSelected' : '')"
                type="button"
                v-on:click="onClickAcctionWithHandlerViewError(rowData, rowIndexTable, 'edit')"
              >
                <ToolTipComponent :value="i18n('rfvue.table.edit')" />
                <i class="far fa-edit"></i>
              </button>

              <button
                v-if="renderDelete"
                :class="'TableButtonComponent ' +(rowIndexTable == rowIndex ? ' TableButtonComponentSelected' : '')"
                type="button"
                v-on:click="onClickAcctionWithHandlerViewError(rowData, rowIndexTable, 'delete')"
              >
                <ToolTipComponent :value="i18n('rfvue.table.delete')" />
                <i class="far fa-trash-alt"></i>
              </button>

              <slot
                name="afterRowAcctionsTable"
                v-bind:rowData="rowData"
                v-bind:rowIndex="rowIndex"
              ></slot>
            </div>

            <slot name="actionsRowTable" v-bind:rowData="rowData" v-bind:rowIndex="rowIndex"></slot>

            <div
              v-for="(column, indexColumn) in columns"
              :key="indexColumn"
              :class="(rowIndexTable % 2 == 0 ? ' TableRowComponent TableRowsComponentPar' : ' TableRowComponent TableRowsComponentInPar')"
              :style="styleRow(rowData, false, 'width: ' + column.width + 'px;')"
            >
              <div class="TableRowTitleComponent">
                {{getRowValue(rowData, column)}}
                <!-- <ng-container [ngSwitch]="column.keyLength">

                            <ng-container *ngSwitchCase="1">
                              {{(rowData || {})[column.keySplit[0]]}}
                            </ng-container>

                            <ng-container *ngSwitchCase="2">
                              {{((rowData || {})[column.keySplit[0]] || {})[column.keySplit[1]]}}
                            </ng-container>

                            <ng-container *ngSwitchCase="3">
                              {{(((rowData || {})[column.keySplit[0]] || {})[column.keySplit[1]] || {})[column.keySplit[2]]}}
                            </ng-container>

                </ng-container>-->
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--Paginator-->
      <div class="TablePaginatorComponent" v-if="hasData()">
        <div class="TablePaginatorSumaryComponent">
          <div
            class="TablePaginatorSumaryRecordsComponent"
          >{{i18n('rfvue.table.totalRecords', [totalRecords])}}</div>
        </div>

        <div
          class="TablePaginatorButtonsComponent"
          v-if="hasPaginator && totalRecords > 0 && numberOfPages > 0"
        >
          <div class="TablePaginatorPageComponent">
            <button
              class="TablePaginatorPageButtonComponent"
              v-on:click="onClickFirstWithHandlerViewError()"
            >
              <i class="fas fa-angle-double-left"></i>
            </button>
          </div>

          <div class="TablePaginatorPageComponent">
            <button
              class="TablePaginatorPageButtonComponent"
              v-on:click="onClickPrevWithHandlerViewError()"
            >
              <i class="fas fa-angle-left"></i>
            </button>
          </div>

          <div
            v-for="pagePaginator in pages"
            :key="pagePaginator"
            class="TablePaginatorPageComponent"
          >
            <button
              v-on:click="onClickPageWithHandlerViewError(pagePaginator)"
              :class="(pagePaginator == page ? 'TablePaginatorPageButtonComponent TablePaginatorPageButtonNumberComponent' : 'TablePaginatorPageButtonComponent')"
            >{{pagePaginator}}</button>
          </div>

          <div class="TablePaginatorPageComponent">
            <button
              class="TablePaginatorPageButtonComponent"
              v-on:click="onClickNextWithHandlerViewError()"
            >
              <i class="fas fa-angle-right"></i>
            </button>
          </div>
          <div class="TablePaginatorPageComponent">
            <button
              class="TablePaginatorPageButtonComponent"
              v-on:click="onClickLastWithHandlerViewError()"
            >
              <i class="fas fa-angle-double-right"></i>
            </button>
          </div>
        </div>
      </div>
      <div
        v-if="!hasData()"
        class="TableComponentEmptyData"
      >{{i18n('rfvue.table.emptyData' , [totalRecords])}}</div>
    </div>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";
import CommonsUtils from "../../utils/CommonsUtils";
import StringUtils from "../../utils/StringUtils";
import Order from "../../beans/Order";
import ConstantsStateAccess from "../../constants/ConstantsStateAccess";
import ToolTipComponent from "../../components/tooltip/ToolTipComponent";
export default {
  extends: BaseComponent,
  name: "TableComponent",
  components: {
    ToolTipComponent
  },
  props: {
    // Columns view for table
    columnsView: {
      default: null
    },
    // Data no lazy for table. Data must be array
    dataNoLazy: {
      default: null
    },
    // Base controller lazy load data
    baseControllerLazyLoad: {
      type: Object,
      default: null
    },
    // render button accionts
    hasActions: {
      type: Boolean,
      default: true
    },
    // render select button
    renderSelect: {
      type: Boolean,
      default: false
    },
    // Render read button
    renderRead: {
      type: Boolean,
      default: true
    },
    // Render edit button
    renderEdit: {
      type: Boolean,
      default: true
    },
    // Render delete button
    renderDelete: {
      type: Boolean,
      default: true
    },
    // Style column actions
    styleActions: {
      type: String,
      default: ""
    },
    // Tipe view show table
    typeView: {
      type: String,
      default: "Table"
    },
    // disable dbl click row actions
    disableClickRowDblActions: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    /**
     * Method to get width actions column
     */
    getWidthActions: function() {
      let style = "";
      if (StringUtils.isBlank(this.styleActions)) {
        let start = 0;
        const columnFactor = 30;
        if (this.renderRead) {
          start = start + columnFactor;
        }
        if (this.renderEdit) {
          start = start + columnFactor;
        }
        if (this.renderDelete) {
          start = start + columnFactor;
        }
        if (this.showSelectButton()) {
          start = start + columnFactor;
        }
        if (start === 0 || start < 60) {
          start = 60;
        }
        
        style = " width:" + start + "px;";
      }
      return style;
    },
    /**
     * Method to know has columns
     * @returns true if has columns false if not
     */
    hasColumns: function() {
      return CommonsUtils.arrayNotNull(this.columns);
    },

    /**
     * Method to know has data
     * @returns true if has data false if not
     */
    hasData: function() {
      return CommonsUtils.arrayNotNull(this.dataInView);
    },
    /**
     * Method to know has data and columns
     * @returns true if has data and colums false if not
     */
    hasColumnsAndData: function() {
      return this.hasColumns() && this.hasData();
    },
    /**
     * Method to set style heigth for row
     */
    styleHeightRow: function() {
      if (CommonsUtils.isNotNull(this.heigthRows) && this.heigthRows > 0) {
        this.styleHeightRowInView = "height:" + this.heigthRows + "px;";
      }
    },
    /**
     * Method for get rowValue
     * @param rowData row data get to get value
     * @param column column for row
     * @returns data from row
     */
    getRowValue: function(rowData, column) {
      let dataReturn = null;
      try {
        switch (column.keyLength) {
          case 1:
            dataReturn = rowData[column.keySplit[0]];
            break;
          case 2:
            dataReturn = rowData[column.keySplit[0]][column.keySplit[1]];
            break;
          case 3:
            dataReturn =
              rowData[column.keySplit[0]][column.keySplit[1]][
                column.keySplit[2]
              ];
            break;
        }
      } catch (ex) {
        dataReturn = null;
      }
      return dataReturn;
    },

    /**
     * Method to get icon sortable column
     * @param column column
     * @returns class icon sort table
     */
    classColumnSortableIcon: function(column) {
      let styleIcon = "TableComponentHeaderIconNoShow";

      if (CommonsUtils.arrayNotNull(this.orders)) {
        const type = this.getSortDirection(column.key);

        if (type != null) {
          if (type === "ASC") {
            styleIcon = "fas fa-sort-up";
          }
        } else {
          styleIcon = "fas fa-sort-down";
        }
      } else {
        styleIcon = "fas fa-sort";
      }

      return styleIcon;
    },
    /**
     * Method to get sort direcction
     * @param key is the key for sort direcction
     * @returns sort directorio for column
     */
    getSortDirection: function(key) {
      let sortDirectorion = null;
      let order = null;
      for (let i = 0; i < this.orders.length; i++) {
        order = this.orders[i];
        if (order.key === key) {
          sortDirectorion = order.type;
          break;
        }
      }
      return sortDirectorion;
    },
    /**
     * On click column header
     * @param column
     */
    onClickColumnHeader: async function(column) {
      if (CommonsUtils.isNotNull(column) && column.sortable && this.lazyLoad) {
        let order = null;
        let indexOrder = null;
        const type = column.sortDirecction;

        for (let i = 0; i < this.orders.length; i++) {
          order = this.orders[i];
          if (order.property === column.key) {
            indexOrder = i;
            break;
          }
        }

        if (type != null) {
          if (type === "ASC") {
            column.sortDirecction = "DESC";
          } else if (type === "DESC") {
            column.sortDirecction = null;
          }
        } else {
          column.sortDirecction = "ASC";
        }

        if (indexOrder != null) {
          if (column.sortDirecction != null) {
            this.orders[indexOrder].type = column.sortDirecction;
          } else {
            this.orders.splice(indexOrder, 1);
          }
        } else if (indexOrder == null && column.sortDirecction != null) {
          this.orders.push(new Order(column.key, column.sortDirecction));
        }

        this.baseControllerLazyLoad.orders = this.orders;
        await this.baseControllerLazyLoad.loadDataTable();
      }
    },
    /**
     * Method to reset orders
     */
    resetOrders: function() {
      this.orders = [];
      for (let i = 0; i < this.columns.length; i++) {
        this.columns[i].sortDirecction = null;
      }
    },

    /**
     * On dbl click row
     * @param rowData
     * @param rowIndexTable
     */
    dblClickRow: function(rowData, rowIndexTable) {
      if (this.lazyLoad && !this.disableClickRowDblActions) {
        this.onClickAcctionWithHandlerViewError(
          rowData,
          rowIndexTable,
          this.baseControllerLazyLoad.isModal()
            ? ConstantsStateAccess.SELECT
            : ConstantsStateAccess.READ
        );
      }
    },
    /**
     * On click action with error handler
     * @param rowData
     * @param position
     * @param state
     */
    onClickAcctionWithHandlerViewError: async function(
      rowData,
      position,
      state
    ) {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.onClickAcction(rowData, position, state);
      });
    },
    /**
     * On click action
     * @param rowData
     * @param position
     * @param state
     */
    onClickAcction: async function(rowData, position, state) {
      if (this.lazyLoad) {
        if (StringUtils.isNotBlank(state)) {
          this.baseControllerLazyLoad.element = rowData;
          this.baseControllerLazyLoad.first =
            (this.baseControllerLazyLoad.page - 1) *
              this.baseControllerLazyLoad.recordsPage +
            position;
          // If is modal select row
          switch (state) {
            case ConstantsStateAccess.SELECT:
              await this.baseControllerLazyLoad.selectDatatableModal(rowData);
              break;
            case ConstantsStateAccess.EDIT:
              await this.baseControllerLazyLoad.goEdit();
              break;
            case ConstantsStateAccess.ADD:
              await this.baseControllerLazyLoad.goAdd();
              break;
            case ConstantsStateAccess.LIST:
              await this.baseControllerLazyLoad.goList();
              break;
            case ConstantsStateAccess.DELETE:
              await this.baseControllerLazyLoad.goDelete();
              break;
            case ConstantsStateAccess.READ:
              await this.baseControllerLazyLoad.goRead();
              break;
          }
        }
      }
    },
    /**
     * Method to cach row
     * @param rowData
     * @param rowIndex
     */
    clickRow: function(rowData, rowIndex) {
      this.selectedValue = rowData;
      this.rowIndex = rowIndex;
    },
    /**
     * Method for go page lazy with error view handler
     * @param page
     */
    goPageWithHandlerViewError: async function(page) {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.goPage(page);
      });
    },
    /**
     * Method for go page lazy
     * @param page
     */
    goPage: async function(page) {
      await this.baseControllerLazyLoad.goPage(page);
    },
    /**
     * Event produced when clikc page button with error view handler
     * @param page
     */
    onClickPageWithHandlerViewError: async function(page) {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.onClickPage(page);
      });
    },
    /**
     * Event produced when clikc page button
     * @param page
     */
    onClickPage: async function(page) {
      await this.goPage(page);
    },
    /**
     * Event produced when clikc first button with error view handler
     */
    onClickFirstWithHandlerViewError: async function() {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.onClickFirst();
      });
    },
    /**
     * Event produced when clikc first button
     */
    onClickFirst: async function() {
      await this.goPage(1);
    },
    /**
     * Event produced when clikc prev button with error view handler
     */
    onClickPrevWithHandlerViewError: async function() {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.onClickPrev();
      });
    },
    /**
     * Event produced when clikc prev button
     */
    onClickPrev: async function() {
      const goPage = this.page - 1;
      if (goPage > 0) {
        await this.goPage(goPage);
      }
    },
    /**
     * Event produced when clikc last button with error view handler
     */
    onClickLastWithHandlerViewError: async function() {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.onClickLast();
      });
    },
    /**
     * Event produced when clikc last button
     */
    onClickLast: async function() {
      await this.goPage(this.numberOfPages);
    },
    /**
     * Event produced when clikc next button with error view handler
     */
    onClickNextWithHandlerViewError: async function() {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.onClickNext();
      });
    },
    /**
     * Event produced when clikc next button
     */
    onClickNext: async function() {
      const goPage = this.page + 1;
      if (goPage <= this.numberOfPages) {
        await this.goPage(goPage);
      }
    },
    /**
     * Method for apply style for row
     * @param rowData is row data for apply data
     * @param actions tru if column actions false if not
     * @param otherStyle other style apply in row
     * @returns style for row
     */
    styleRow: function(rowData, actions, otherStyle) {
      let style = "";
      // Set style acctions
      if (actions) {
        style = this.getWidthActions();
        if (StringUtils.isBlank(style)) {
          style = "";
        }
      }
      if (StringUtils.isNotBlank(otherStyle)) {
        style = otherStyle;
      }
      // Set style for row
      if (this.lazyLoad) {
        style = this.baseControllerLazyLoad.styleRow(style, rowData);
      }
      return style;
    },
    /**
     * Method for known show select button
     * @returns true if show select button false if not
     */
    showSelectButton: function() {
      let render = this.renderSelect;
      if (!render && CommonsUtils.isNotNull(this.baseControllerLazyLoad)) {
        render = this.baseControllerLazyLoad.isModal();
      }
      return render;
    }
  },
  created: async function() {
    this.columns = this.columnsView;
    // If is lazy set controller for manage data and properties for table
    if (CommonsUtils.isNotNull(this.baseControllerLazyLoad)) {
      if (CommonsUtils.arrayNotNull(this.baseControllerLazyLoad.columns)) {
        this.columns = this.baseControllerLazyLoad.columns;
      }
      this.hasPaginator = true;
      this.lazyLoad = true;
      this.baseControllerLazyLoad.tableComponent = this;
      await this.baseControllerLazyLoad.loadDataTable();
    }
    this.styleHeightRow();
  },
  data() {
    return {
      columns: [],
      styleHeightRowInView: "",
      orders: [],
      dataInView: [],
      hasPaginator: false,
      rowIndex: -1,
      selectedValue: null,
      numberOfPages: 0,
      page: 1,
      pages: [],
      pageVisibles: 5,
      first: 0,
      recordsPage: 50,
      dlazyLoad: false,
      totalRecords: 0
    };
  }
};
</script>

<style scoped>
.TableComponent {
  float: left;
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.TableComponentHeader {
  float: left;
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.TableComponentRow,
.TableComponentHeaders {
  float: left;
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: var(--rf-tableRowFontColor, #466289);
  background-color: var(--rf-tableRowBackColor, white);
}

.TableComponentHeaders {
  color: var(--rf-tableHeaderFontColor, white);
  background-color: var(--rf-tableHeaderBackColor, #466289);
}

.TableWrapperComponent {
  width: 100%;
  overflow: auto;
}

.TableHeadersComponent,
.TableRowsComponent {
  float: left;
  clear: left;
  width: auto;
  min-width: 100%;
  display: flex;
}

.TableHeadersComponent {
  background-color: var(--rf-tableHeaderBackColor, #466289);
}

.TableComponentHeader {
  color: var(--rf-tableHeaderFontColor, white);
  background-color: var(--rf-tableHeaderBackColor, #466289);
  height: 28px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.07);
  min-width: 20px;
  float: left;
}

.TableComponentHeaderSortable {
  cursor: pointer;
}

.TableComponentHeaderTitle {
  margin-top: 5px;
  text-align: center;
}

.TableComponentHeaderIconNoShow {
  display: none;
}

.TableRowComponent {
  float: left;
  height: 22px;
  border-right: transparent;
  border-bottom: transparent;
  /* margin-left: 2px; */
  margin-bottom: 1px;
  min-width: 20px;
  border: 1px solid var(--rf-tableRowBorderColor, #aeb9c7);
  margin-top: 1px;
  overflow: hidden;
}

.TableRowsComponentPar {
}

.TableRowsComponentInPar {
}

.TableComponentRow {
  /* overflow-y: auto; */
}

.TableRowsComponent {
  cursor: pointer;
}

.TableRowTitleComponent {
  margin-top: 5px;
  margin-left: 5px;
  max-height: 18px;
