import BaseBrowserController from "./BaseBrowserController";
import ConstantsStateAccess from "../constants/ConstantsStateAccess";
import ConstantsModals from "../constants/ConstantsModals";
import CommonsUtils from "../utils/CommonsUtils";
import HttpUtils from "../utils/HttpUtils";
/**
 * Base red controller
 */
export default class BaseRedController extends BaseBrowserController {
  constructor(baseViewController) {
    super(baseViewController);
  }

  /**
   * Method to load new element
   */
  async loadNew() {
    this.element = null;
    const self = this;
    this.clearDataList();
    const responseService = await this.resolveDataAwait(async function() {
      return self.service.loadNew();
    });
    // If no erros, load element else go to list
    if (
      CommonsUtils.isNotNull(responseService.data) &&
      HttpUtils.isStatusCodeFamilyHttpOk(responseService.statusCode)
    ) {
      this.element = responseService.data;
    } else {
      this.addMessageError(this.i18n("rfvue.red.error.loadError"));
      await this.goList();
    }
  }
  /**
   * Method to load edit
   */
  async loadEdit() {
    this.clearDataList();
    await this.loadRead();
  }
  /**
   * Mhetod to load delete
   */
  async loadDelete() {
    super.loadDelete();
    await this.loadRead();
    const params = {};
    params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_COMPONENT] = this;
    params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_YES] = "delete";

    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.baseViewController
          .getVueContext()
          .openConfirmModal(null, null, true, params);
      }, self.baseViewController.tab);
  }
  /**
   * Method to load read
   */
  async loadRead() {
    const self = this;

    const responseService = await this.resolveDataAwait(async function() {
      return self.service.read(self.element);
    });

    // If no erros, load element else go to list
    if (
      CommonsUtils.isNotNull(responseService.data) &&
      HttpUtils.isStatusCodeFamilyHttpOk(responseService.statusCode)
    ) {
      this.clearDataList();
      this.element = responseService.data;
    } else {
      // Only show error when total records greathen than 0
      if (this.totalRecords > 0) {
        this.addMessageError(this.i18n("rfvue.red.error.loadError"));
      }
      await this.goList();
    }
  }
  /**
   * Method to fix element
   * @param element to fix
   */
  fixElement(element) {
    this.element = element;
  }
  /**
   * Method to delete
   */
  async delete() {
    const self = this;
    let closeModal = true;
    const responseService = await this.resolveDataAwait(async function() {
      return self.service.delete(self.element);
    });
    // If no erros, load element else go to list
    if (HttpUtils.isStatusCodeFamilyHttpOk(responseService.statusCode)) {
      this.addMessageSucces(this.i18n("rfvue.red.delete.success", null));
      await this.loadDataTable();
    }else{
      this.addMessageError(this.i18n("rfvue.red.delete.error", null));
    }
    // Go browser if data is zero
    if (
      this.data.length === 0 ||
      this.stateAccess === ConstantsStateAccess.LIST
    ) {
      await this.changeState(ConstantsStateAccess.LIST);
    } else {
      await this.goPosition(this.first === 0 ? 0 : this.first - 1);
    }
    return closeModal;
  }
  /**
   * Method to edit element
   */
  async edit() {
    const self = this;
    // TODO check form validation
    const responseService = await this.resolveDataAwait(async function() {
      return self.service.edit(self.element);
    });
    // If no erros, load element else go to list
    if (HttpUtils.isStatusCodeFamilyHttpOk(responseService.statusCode)) {
      this.addMessageSucces(this.i18n("rfvue.red.save.success", null));
      this.element = responseService.data;
      await this.goRead();
    } else {
      this.addMessageError(this.i18n("rfvue.red.save.error", null));
    }
  }
  /**
   * Method to add element
   */
  async add() {
    const self = this;
    // TODO check form validation
    const responseService = await this.resolveDataAwait(async function() {
      return self.service.add(self.element);
    });
    // If no erros, load element else go to list
    if (HttpUtils.isStatusCodeFamilyHttpOk(responseService.statusCode)) {
      this.addMessageSucces(this.i18n("rfvue.red.save.success", null));
      this.element = responseService.data;
      await this.loadDataTable();
      await this.goRead();
    } else {
      this.addMessageError(this.i18n("rfvue.red.save.error", null));
    }
  }

  /**
   * Method to cancel form with handler view error
   */
  async cancelWithHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.cancel();
      });
  }

  /**
   * Method to cancel form
   */
  async cancel() {
    this.element = this.oldElement;
    await this.goRead();
  }

  /**
   * Method for save data with handler view error
   */
  async saveWithHandlerViewError() {
    const self = this;
    self.baseViewController
      .getVueContext()
      .applyAsyncFunctionWithHandlerViewError(() => {
        return self.save();
      });
  }
  /**
   * Method for save data
   */
  async save() {
    this.baseViewController.showWaitStatus(this.tab);
    let valid = await this.validateContainerView();
    if (CommonsUtils.isNull(valid) || valid) {
      switch (this.stateAccess) {
        case ConstantsStateAccess.ADD:
          if (await this.validateAdd()) {
            await this.add();
          }
          break;

        case ConstantsStateAccess.EDIT:
          if (await this.validateEdit()) {
            await this.edit();
          }
          break;
      }
    }
    this.baseViewController.hideWaitStatus(this.tab);
  }
  /**
   * Method for validate add
   * @returns true if valid false if not
   */
  async validateAdd() {
    return true;
  }
  /**
   * Method for validate edit
   * @returns true if valid false if not
   */
  async validateEdit() {
    return true;
  }
  /**
   * Method to know element is not null
   */
  isElementNotNull() {
    return CommonsUtils.isNotNull(this.element);
  }
}
