<!--
    Component card
-->
<template>
  <div class="CardComponent" v-bind:id="getId()" :key="getId()">
    <div v-bind:class="'CardComponentHeader'+(fullDiv ? 'FullDiv' : '')" v-on:click="onClickHeader">
      <div class="CardComponentHeaderIcon">
        <i class="fas fa-caret-up" v-bind:id="getId() + 'Icon' " :key="getId() + 'Icon' "></i>
      </div>
      <div class="CardComponentHeaderTitle">{{title}}</div>
    </div>

    <div
      v-bind:class="'CardComponentBody'+(fullDiv ? 'FullDiv' : '')"
      v-bind:id="getId() + 'Body'"
      :style="styleBody"
    >
      <slot name="cardContentTemplate"></slot>
    </div>
  </div>
</template>
<script>
import BaseComponent from "../base/BaseComponent";
import CommonsUtils from "../../utils/CommonsUtils";
export default {
  extends: BaseComponent,
  name: "CardComponent",

  props: {
    fullDiv: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    },
    styleBody: {
      type: String,
      default: ""
    },
    containerState: {
      type: Object,
      default: () => ({
        open: true
      })
    }
  },

  created: function() {
    this.openCloseCard();
  },

  methods: {
    openCloseCard: function() {
      const header = document.getElementById(this.getId() + "Icon");
      const body = document.getElementById(this.getId() + "Body");

      if (CommonsUtils.isNotNull(header)) {
        if (this.containerState.open) {
          header.classList.remove("fa-caret-down");
          header.classList.add("fa-caret-up");
          body.style.display = "block";
        } else {
          header.classList.remove("fa-caret-up");
          header.classList.add("fa-caret-down");
          body.style.display = "none";
        }
      }
    },
    /**
     * Function execute when click header
     */
    onClickHeader: function() {
      this.containerState.open = !this.containerState.open;
      this.openCloseCard();
      // if (header.classList.contains("fa-caret-up")) {
      //   header.classList.remove("fa-caret-up");
      //   header.classList.add("fa-caret-down");
      //   body.style.display = "none";
      // } else {
      //   header.classList.remove("fa-caret-down");
      //   header.classList.add("fa-caret-up");
      //   body.style.display = "block";
      // }
    }
  }
};
</script>
<style scoped>
.CardComponent {
  float: left;
  width: 100%;
  margin-bottom: 5px;
}

.CardComponentHeader,
.CardComponentHeaderFullDiv {
  width: calc(100% - 20px);
  float: left;
  clear: left;
  height: 30px;
  background-color: var(--rf-cardHeaderBackColor, #466289);
  text-indent: 10px;
  cursor: pointer;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
}

.CardComponentHeaderFullDiv {
  margin: 0;
  width: 100%;
}

.CardComponentHeaderTitle {
  font-weight: bold;
  margin-left: 0px;
  margin-top: 6px;
  text-transform: uppercase;
  float: left;
  color: var(--rf-cardHeaderFontColor, white);
}

.CardComponentHeaderIcon {
  margin-left: 5px;
  margin-top: 6px;
  font-weight: bold;
  float: left;
  color: var(--rf-cardHeaderFontColor, white);
}

.CardComponentBody,
.CardComponentBodyFullDiv {
  float: left;
  clear: left;
  padding-top: 10px;
  width: calc(100% - 62px);
  margin-left: 10px;
  margin-right: 10px;
  /* margin-bottom: 10px; */
  background-color: var(--rf-cardBodyBackColor, white);
  padding: 20px;
  border: 1px solid var(--rf-cardBorderColor, #466289);
  border-radius: 2px;
  overflow: auto;
}

.CardComponentBodyFullDiv {
  width: calc(100% - 42px);
  margin: 0;
}
</style>